import { Box, Image, Text } from "@chakra-ui/react";

function Skills(){
   return (
       <Box>
         <Box>
            <Text mb={"60px"} color={"#FFB6C1"} mt={20} textDecoration={"underline"} fontSize={{base:"30px",md:"45px",lg:"60px"}}>Technical Skills</Text>
        </Box>

        <Box  margin={"auto"} mb="80px"  w="80%" justifyContent="center" alignContent={"center"} className="project">
        <Box textAlign="center">
                <Text color={"#FFB6C1"} fontSize={{base:"21px",md:"27px",lg:"38px"}} pt={30}>FrontEnd</Text>
            </Box>
            <Box display={"grid"}  gridTemplateColumns={{base:"repeat(2,1fr)",md:"repeat(2,1fr)",lg:"repeat(5,1fr)"}}  gap="50px" mt={"70px"} >
               <Image className="skills" w={{base:"80px",md:"150px",lg:"200px"}} src="https://raw.githubusercontent.com/devicons/devicon/master/icons/css3/css3-original-wordmark.svg"></Image>
               <Image className="skills" w={{base:"80px",md:"150px",lg:"200px"}} src="https://raw.githubusercontent.com/devicons/devicon/master/icons/html5/html5-original-wordmark.svg"></Image>
               <Image className="skills" w={{base:"70px",md:"140px",lg:"150px"}} src="https://raw.githubusercontent.com/devicons/devicon/master/icons/javascript/javascript-original.svg"></Image>
               <Image className="skills" w={{base:"70px",md:"140px",lg:"180px"}} src="https://raw.githubusercontent.com/devicons/devicon/master/icons/react/react-original-wordmark.svg"></Image>
               <Image className="skills" w={{base:"70px",md:"140px",lg:"180px"}} src="https://raw.githubusercontent.com/devicons/devicon/master/icons/redux/redux-original.svg"></Image>
            </Box>
        </Box>


        <Box  margin={"auto"}  w="80%" justifyContent="center" alignContent={"center"} className="project">
        <Box textAlign="center">
                <Text color={"#FFB6C1"} fontSize={{base:"21px",md:"27px",lg:"38px"}} pt={30}>Backend</Text>
            </Box>
            <Box display={"grid"}  gridTemplateColumns={{base:"repeat(2,1fr)",md:"repeat(2,1fr)",lg:"repeat(3,1fr)"}}  gap="30px" mt={"70px"}>
               <Image className="skills" w={{base:"100px",md:"200px",lg:"300px"}} src="data:image/png;base64,"></Image>
               <Image className="skills" w={{base:"100px",md:"200px",lg:"300px"}} src="http://4.bp.blogspot.com/-R2D3RLQuP_U/VAAWQqa1GdI/AAAAAAAAAuw/GqaHO06qcl0/s1600/mango%2BDB.PNG"></Image>
               <Image className="skills" w={{base:"100px",md:"200px",lg:"300px"}} src="https://searchlogovector.com/wp-content/uploads/2018/08/express-inc-logo-vector.png"></Image>
            </Box>
          </Box>
        </Box>
   )

}

export default Skills;